.post{
   width:385px ; 
   margin: 0px 25px 40px 25px;
}
.postImg{
    width: 100%;
    height: 280px;
    object-fit: cover;
    border-radius: 8px;
}
.postInfo{
    display: flex;
    flex-direction: column;
    align-items: center;
}
.postCat{
font-family: 'Varela Round',
sans-serif;
font-size: 18px;
color: #be9656 ;
line-height: 20px;
margin-top: 15px;
margin-right: 10px;
cursor: pointer;
}
.postTitle{
font-family: 'Josefin Sans',
sans-serif;
font-size: 24px;
font-weight: 700;
margin-top: 15px;
cursor: pointer;
}
.postData{
    font-family: 'Lora',
    serif;
    font-style: italic;
    font-size: 15px;
    color: #999;
    margin-top: 15px;
}
.postDescription{
    font-family: 'Varela Round',
    sans-serif;
    font-style: 14px;
    color:#444 ;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 4;
    -webkit-box-orient: vertical;
}