.sidebar{
    flex: 3;
    margin: 20px;
    padding-bottom: 30px;
    background-color: #fdfbfb;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
     align-items: center;
}
.sidebarItem{
    display: flex;
    align-items: center;
    flex-direction: column;
}
.sidebatTitle{
    margin: 10px;
    padding: 5px;
    width: 80%;
    border-top: 1px solid #a7a4a4;
    border-bottom: 1px solid #a7a4a4;
    font-family: 'Varela Round',
    sans-serif;
    font-size: 20px;
    color: #222;
    font-weight: 600;
    line-height: 20px;
    text-align: center;
}
.sidebarItem > img{
    margin-top: 25px;
    border-radius: 500px;
}
.sidebarItem > p{
    padding: 30px;
}
.sidebarList{
    list-style: none;
    margin-bottom: 30px;
}

.sidebarListItem{
    display: inline-block;
    width: 50%;
    margin-top: 17px;
    cursor: pointer;
}
.sidebarSocial{
    margin-top:15px ;
    width: 250px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.sidebarIcon{
    font-size: 24px;
     margin-left: 10px;
     cursor: pointer;
}