.write{
padding-top: 50px;
}

.writeImg {
    margin-left: 150px;
    width: 74vw;
    height: 260px;
    border-radius: 10px;
    object-fit: cover;
}

.writeForm{
position: relative;
}
.writeFormGroup{
    margin-left: 150px;
    display: flex;
    align-items: center;
    
}
.writeIcon{
    width: 25px;
    height: 25px;
    border-radius: 50%;
    border: 1px solid;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 22px;
    color: rgb(107, 105, 105) ;
    cursor: pointer;
}
.writeInput{
    font-size: 30px;
    border: none;
    padding: 20px;
    width: 70vw;
}
.writeInput:focus{
outline: none;
}
.writeText{
    font-size: 22px;
    height: 100vh;
}
.writeSubmit{
    position: absolute;
    top: 20px;
    right: 50px;
    color: white;
    background-color: teal;
    padding: 14px;
    border: none;
    border-radius: 10px;
    cursor: pointer;
    font-size: 16px;
}
